<template>
  <div id="app">
    <router-view />
    <TodoComponent />
    <hr>
    <h1>Test Data</h1>
    <p>{{ testData }}</p>
  </div>
</template>

<script>
import TodoComponent from "./components/TodoComponent.vue";
export default {
  name: "App",
  data() {
    return {
      
      testData: "",
    };
  },
  components: {
    TodoComponent,
  },
  methods: {
      async fetchUserData(id) {
        // fetch 함수는 Promise를 반환합니다.
        const response = await fetch(`http://localhost:3002/todos/${id}`);

        // response.json()도 Promise를 반환하므로, 이를 처리하기 위해 await를 사용합니다.
        const data = await response.json();

        return data;
      },
    },
    created() {
      // 함수를 호출하고, 반환된 Promise를 처리합니다.
      this.fetchUserData("47d3").then((data) => {
        this.testData = data;
      });
    },
};
</script>
